#hm_canvas .canvas_off {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
}
#hm_canvas.is_opened .canvas_off {
  opacity: 1;
  visibility: visible;
}
#hm_canvas .canvas_body {
  position: fixed;
  top: 0;
  z-index: 3;
  overflow-y: scroll;
}
#hm_canvas[data-hm_menu_in=fede_in] .canvas_body {
  left: 0;
  opacity: 0;
  visibility: hidden;
}
#hm_canvas[data-hm_menu_in=fede_in].is_preloaded .canvas_body {
  transition: opacity 0.3s visibility 0.3s;
}
#hm_canvas[data-hm_menu_in=fede_in].is_opened .canvas_body {
  opacity: 1;
  visibility: visible;
}
#hm_canvas[data-hm_menu_in=slide_from_top] .canvas_body {
  left: 0;
  transform: translateY(-100%);
}
#hm_canvas[data-hm_menu_in=slide_from_top].is_preloaded .canvas_body {
  transition: transform 0.6s cubic-bezier(0.215, 0.61, 0.355, 1);
}
#hm_canvas[data-hm_menu_in=slide_from_top].is_opened .canvas_body {
  transform: translateY(0);
}
#hm_canvas[data-hm_menu_in=slide_from_left] .canvas_body {
  left: 0;
  transform: translateX(-100%);
}
#hm_canvas[data-hm_menu_in=slide_from_left].is_preloaded .canvas_body {
  transition: transform 0.6s cubic-bezier(0.215, 0.61, 0.355, 1);
}
#hm_canvas[data-hm_menu_in=slide_from_left].is_opened .canvas_body {
  transform: translateX(0);
}
#hm_canvas[data-hm_menu_in=slide_from_right] .canvas_body {
  right: 0;
  transform: translateX(100%);
}
#hm_canvas[data-hm_menu_in=slide_from_right].is_preloaded .canvas_body {
  transition: transform 0.6s cubic-bezier(0.215, 0.61, 0.355, 1);
}
#hm_canvas[data-hm_menu_in=slide_from_right].is_opened .canvas_body {
  transform: translateX(0);
}